import diyTitle from '../../components/diyTitle/diyTitle.vue';
import brandInfo from './components/brandInfo.vue';
import TopDropdown from '../../components/lists/TopDropdown.vue';
import goods from '../../mock/goods.js';
import uniLoadMore from '@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
export default {
	onLoad(options) {
		uni.getSystemInfo({
			success: res => {
				this.cateHeight = res.windowHeight;
			}
		});
	},
	data() {
		return {
			orderLists: goods.orderLists,
			dropScreenShow: false,
			selectIndex: -1, //选择分类
			floorFliter: [], //楼层筛选
			cateHeight: 0,
			cateIndex: 0,
			tabData: {
				list: ['楼层', '分类'],
				index: 0
			},
			floorList: [], //楼层列表
			category: [], //分类树
			listParam: {},
			loadStatus: 'loading'
		};
	},
	mounted() {
		this.getFloor(true);
		this.getTree();
		this.getFloorId();
	},
	onReachBottom() {
		if (this.listParam.page == this.listParam.lastPage) {
			this.loadStatus = 'noMore';
		} else {
			this.listParam.page++;
			setTimeout(() => {
				this.getFloor();
			}, 300);
		}
	},
	methods: {
		getFloor(init) {
			if (init) {
				this.floorList = [];
				this.listParam.page = 1;
				this.listParam.lastPage = 1;
				this.loadStatus = 'loading';
			}
			this.$http({
				aurl: 'brandList',
				data: this.listParam,
				success: res => {
					console.log(res)
					let list = res.data.result.lists.data;
					let lastPage = res.data.result.lists.last_page;
					if (init) {
						if (lastPage === 1) {
							this.loadStatus = 'noMore';
						} else {
							this.listParam.lastPage = lastPage;
						}
					}
					if (list.length !== 0) {
						list.forEach(item => {
							this.floorList.push(item);
						});
					}
				},
				fail: err => {
					console.log(err);
				},
				complete: () => {}
			});
		},
		getTree() {
			this.$http({
				aurl: `brandCategory`,
				success: res => {
					let tree = res.data.result;
					if (tree.length !== 0) {
						this.category = tree;
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		getFloorId() {
			this.$http({
				aurl: 'floorId',
				success: res => {
					console.log(res.data);
					let idlist = res.data.result;
					if (idlist.length !== 0) {
						idlist.forEach(item => {
							item.selected = false;
							this.floorFliter.push(item);
						});
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		searchKw(e) {
			//搜索
			this.listParam = {};
			this.listParam.keyword = e.detail.value;
			this.getFloor(true);
		},
		onChangeIndex(e) {
			let index = e.currentTarget.dataset.index;
			this.tabData.index = index;
			if (index === 0) {
				this.dropScreenShow = !this.dropScreenShow;
			}
		},
		onClickItem(index) {},
		phone(e) {
			console.log('打电话。。');
		},
		like(index) {
			this.floorList[index].like = !this.floorList[index].like;
			
			console.log('linke。。' + index);
		},
		changeCate(e) {
			let index = e.currentTarget.dataset.index;
			this.cateIndex = index;
		},
		btnCloseDrop() {
			//关闭蒙版
			this.scrollTop = 1;
			this.$nextTick(() => {
				this.scrollTop = 0;
			});
			this.dropScreenShow = false;
			this.attrIndex = -1;
		},
		btnSelected(e) {
			//选择楼层
			let index = e.currentTarget.dataset.index;
			let item = this.floorFliter[index];
			item.selected = !item.selected;
			this.$set(this.floorFliter, index, item);
		},
		btnReset() {
			//筛选重置
			this.floorFliter.map((item, index) => {
				this.$set(this.floorFliter[index], 'selected', false);
			});
		},
		btnSure() {
			//筛选楼层
			let selectArr = [];
			this.listParam = {};
			this.listParam.floors_id_arr = [];
			this.floorFliter.map((item, index) => {
				if (item.selected) {
					this.listParam.floors_id_arr.push(item.id);
				}
			});
			this.getFloor(true);
			this.btnCloseDrop();
		},
		floorCat(e) {
			//分类楼层
			this.listParam = {};
			let cat_id = e.currentTarget.dataset.cat_id;
			this.listParam.rel_cat_id = cat_id;

			console.log(this.listParam);
			this.getFloor(true);
			this.tabData.index = 0;
		},
		toList() {
			uni.navigateTo({
				url: './lists'
			});
		},
		tapItem(index) {
			let shopid = this.floorList[index].id;
			uni.navigateTo({
				url: `/pages/brand/lists?shopid=${shopid}`
			});
		}
	},
	components: {
		brandInfo,
		TopDropdown,
		uniLoadMore
	}
};
